body {
  font-family: "Roboto", sans-serif;
  font-size: 12px;

 
}
  h1,
  h2,
  h3,
  h4,
  h5 {
    font-family: "Roboto", sans-serif;
    font-family: cursive;
    color: black;
    margin-bottom: 30px;

  }
  p {
    font-weight: 500;
    font-size: 16px;
    text-align: center;
  }

nav {
  background-color:  #efebe9;
  color: #CA397D;
}
.identity {
  color: #CA397D;
  text-align: center;
}

nav .brand-logo {
  color: #CA397D;
  font-family: cursive;
  font-size: 2.1rem;
  padding: -3px;
  margin-left: 10px;
  }

nav ul a {
    -webkit-transition: background-color .3s;
    -o-transition: background-color .3s;
    transition: background-color .3s;
    -webkit-transition: all ease-in-out 0.4s;
    -o-transition: all ease-in-out 0.4s;
    transition: all ease-in-out 0.4s;
    font-size: 14px;
    color: #CA397D !important;
    display: block;
    padding: 0 15px;
    cursor: pointer;
}

.parallax {
  background-image: url('../assets/img/about.jpg');
 }

.about-container{  
   background: -webkit-linear-gradient( left,rgba(255,0,0,0), rgba(0, 0, 255, 0.7) );
   margin-bottom: -20px;
 }

.modify-nav {
  color: #000;
}

input {
  width: 40%;
  height:80%;
}

.bold-span {
  font-size: 30px;
  font-weight: 30px;
}

.form-text {
  font-weight: bold;
  font-size: 18px;
  color: #000000;
}

.bold {
  font-weight: bold;
  font-size: 60px;
  text-align: center;
  color: #CA397D !important;;
}
.welcome{
    text-align: center;
    font-size: 20px;
    color: #000000;
}

.socials {
  text-align: center;
}

@media only screen and (min-width: 993px){
.row .col.l6 {
    width: 100%;
    margin-left: auto;
    left: auto;
    right: auto;
}
}

.about-me {
  font-size: 20px;
}

footer {
  padding: 5px;
  margin-top: 0px;
  border-top: 1px solid #efebe9;
  background-color:#efebe9;
  color: #000000;
}

.blog-main {

  width: 100%;
  position: absolute;
}

.blog-main .landing {
    position: relative;
    top: 200px;
}

.particles-div {
  height: 800px;
}

.social-icon {
  margin-right:5px;
}

.sticky-footer {
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -ms-flex-direction: column;
      flex-direction: column;
}

main {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
}


footer {
  position: fixed;
  bottom: 0;
  width: 100%;
}

footer .socials {
  margin: 0px auto;
  width: 120px;
}
   


